<template>
  <div class="g-orderConfirm after">
    <router-link :to="{name:'addressList',query:{order:'true'}}" tag="div" class="m-address" v-if="addressdetails.len">
      <div v-if="addressdetails.detalis!=''" class="s-msg">
        <span class="name">{{addressdetails.detalis.contact}}</span>
        <span class="phone">{{addressdetails.detalis.phone}}</span>
      </div>
      <div v-if="addressdetails.detalis!==''" class="s-addressDetails beyond">
        {{addressdetails.detalis.province}} {{addressdetails.detalis.city}} {{addressdetails.detalis.area}} {{addressdetails.detalis.address}}
      </div>
      <div v-else class="s-choose">请选择地址</div>
      <i class="s-rightArrow"></i>
    </router-link>
    <router-link :to="{name:'editaddress',query:{order:'true'}}" tag="div" class="m-address" v-else>
      <div class="s-choose">请填写地址</div>
      <i class="s-rightArrow"></i>
    </router-link>
    <div class="m-goodDetails" v-if="good.sortId==1||good.sortId==3"><!--积分元宝商品-->
      <div class="s-img">
        <img :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+good.carousels[0]" alt="">
      </div>
      <div class="s-box">
        <span class="beyond">{{good.proName}}</span>
        <span class="money">{{good.sortId==1||good.sortId==2?'积分':'元宝'}}：{{good.price}}</span>
        <span class="">数量 &nbsp;&nbsp;&nbsp;&nbsp; ×{{bit.number}}</span>
      </div>
    </div>
    <div class="m-goodDetails" v-else><!--众筹商品-->
      <div class="s-img">
        <img :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+good.picMain" alt="">
      </div>
      <div class="s-box">
        <span class="beyond">{{good.title}}</span>
        <span class="money">{{good.sortId==1||good.sortId==2?'积分':'元宝'}}：{{good.activityIntegral}}</span>
        <span class="">数量 &nbsp;&nbsp;&nbsp;&nbsp; ×{{bit.number}}</span>
      </div>
    </div>
    <div class="m-list">
      <div class="s-pay">
        <span>支付方式：</span>
        <span>{{good.sortId==1||good.sortId==2?'积分':'元宝'}}</span>
      </div>
      <div class="s-number">
        <span>运费：</span>
        <span v-if="freight">{{freight}}{{good.sortId==1||good.sortId==2?'积分':'元宝'}}</span>
        <span v-else>包邮</span>
      </div>
      <div class="s-userMsg">
        <span>买家留言：</span>
        <input type="text" placeholder="输入你的要求" v-model="bit.remark">
      </div>
    </div>
    <div v-if="addressdetails.detalis!=''&&addressdetails.len" class="m-submit" @click="showState">提交订单</div>
    <router-link :tag="div" :to="{name:'addressList',query:{order:'true'}}" v-else-if="addressdetails.detalis==''&&addressdetails.len" class="m-submit">选择地址</router-link>
    <router-link :tag="div" :to="{name:'editaddress',query:{order:'true'}}" v-else class="m-submit">请填写地址</router-link>
    <transition name="slide-fade">
      <div class="m-diabox" v-show="showdia">
        <div class="s-title">确认支付</div>
        <div class="s-money" v-if="good.sortId==1||good.sortId==3">{{good.sortId==1||good.sortId==2?'积分':'元宝'}}：{{good.price*bit.number+freight}}</div>
        <div class="s-money" v-else>{{good.sortId==1||good.sortId==2?'积分':'元宝'}}：{{good.activityIntegral*bit.number+freight}}</div>
        <div class="s-button" @click="paying">确认</div>
        <i class="s-close" @click="closeing"></i>
      </div>
    </transition>
    <allbk v-show="showdia" :scolor="'rgba(192,192,192,0.6)'"/>
  </div>
</template>

<script>
  import allbk from '@/components/allBk'
  export default {
    name: "order-confirm",
    data(){
      return {
        showdia:false,
        addressdetails:{
          len:true,
          detalis:''
        },//是否有地址
        good:{},//商品信息
        freight:0,//运费
        bit:{//提交信息
          remark:'',
          number:this.$store.state.orderC.number,
          proId:this.$store.state.orderC.proId,
          addressId:this.$store.state.orderC.addressId,
          username:sessionStorage.getItem('userid')
        },
        orderId:'',//提交订单获取id号
        activityId:this.$store.state.orderC.activityId//套餐id
      }
    },
    created(){
      this.addressList();//地址
      this.goodDetail();//商品详情
    },
    mounted(){
    },
    methods:{
      showState(){
        if(this.activityId){
          this.bit.activityId=this.activityId;
        }
        this.$post(this.$bee+'/api/order/confirmOrder',this.bit).then((data)=> {
          if (data.code == 200) {
            this.showdia=true;
            this.orderId=data.data.orderId;
            this.$store.commit("ORDERCLEAR");
          } else {
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      paying(){
        this.$post(this.$bee+'/api/order/payOrder',{username:this.bit.username,orderId:this.orderId}).then((data)=> {
          if (data.code == 200) {
            this.$router.replace({name:"success",query:{orderId:this.orderId}});
          } else {
            this.showdia=false;
            this.$createToast({
              txt: data.message,
              type: 'txt',
              time: 2000,
              onTimeout: () => {
                this.$router.replace({name:"nosuccess",query:{orderId:this.orderId}});
              }
            }).show();
          }
        })
      },
      closeing(){
        this.showdia=false;
        this.$router.replace({name:"nosuccess",query:{orderId:this.orderId}});
      },
      addressList(){
        this.$post(this.$bee+'/api/user/listAddress',{username:this.bit.username}).then((data)=> {
          if (data.code == 200) {
            if(data.data.adverts.length==0){//无地址
              this.addressdetails.len=false;
            }else{//有地址
              this.addressdetails.len=true;
              let _addressId=this.bit.addressId;
              data.data.adverts.forEach((i)=>{
                if(_addressId){//无默认地址
                  if(i.id==_addressId){
                    this.addressdetails.detalis=i;
                  }
                }else{//有默认地址
                  if(i.isDefault){
                    this.addressdetails.detalis=i;
                    this.bit.addressId=i.id;
                  }
                }
              })
            }
            this.yfed();//运费计算
          } else {
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      goodDetail(){
        this.$post(this.$bee+'/api/product/productDetail',{id:this.$store.state.orderC.proId}).then((data)=> {
          if (data.code == 200) {
            if(this.activityId){
              data.data.activitys.forEach((i)=>{
                if(this.activityId==i.id){
                  i.sortId=data.data.pro.sortId;
                  this.good=i;
                }
              })
            }else{
              this.good=data.data.pro;
            }
          } else {
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      yfed(){
        if(!this.bit.addressId){
          return;
        }
        this.$post(
          this.$bee+'/api/order/calculateShipping',
          {number:this.bit.number,addressId:this.bit.addressId,proId:this.bit.proId}
          ).then((data)=> {
          if (data.code == 200) {
           this.freight=data.data.freight;
          } else {
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      }
    },
    components: {
      allbk
    }
  }
</script>

<style scoped lang="stylus">
  .g-orderConfirm
    margin-bottom 100px
  .m-address
    box-sizing border-box
    background #d18146
    height 62.5px
    font-size 13.5px
    color #fff
    text-align left
    padding 10px 25px 10px
    position relative
    margin-bottom 20px
    .s-choose
      height 42.5px
      line-height 42.5px
    .s-msg
      padding-top 5px
      .phone
        margin-left 75px
    .s-addressDetails
      width 290px
      margin-top 7.5px
      padding 5px 0
    .s-rightArrow
      position absolute
      right 37.5px
      top calc(50% - 7.5px)
      width 8px
      height 15px
      background  url(../assets/images/right-arrow.png) no-repeat center center
      background-size 100% 100%
  .m-goodDetails
    box-sizing border-box
    padding 18px 12.5px
    height 95px
    background #f4f4f4
    display flex
    flex-wrap wrap
    .s-img
      width 60px
      height 60px
      img
        width 100%
        height 100%
    .s-box
      width calc(100% - 85px)
      padding-top 2px
      font-size 13px
      color #444444
      text-align left
      padding-left 15px
      span
        display block
        width 100%
        margin-bottom 3px
        padding 2px 0
        &.money
          color #cc5b24
          font-size 14px
  .m-list
    padding 0 12.5px
    &>div
      height 30px
      text-align left
      background url(../assets/images/right-arrow.png) no-repeat center right
      background-size 8px 14px
      border-bottom 0.5px solid #d0d0d0
      span
        display inline-block
        line-height 30px
        color #333
        font-size 12px
        vertical-align top
        &:nth-child(2)
          float right
          margin-right 17.5px
      input
        display inline-block
        vertical-align top
        width 270px
        line-height 29px
        font-size 12px
  .m-submit
    width 350px
    height 40px
    line-height 40px
    background #db3838
    position fixed
    bottom 10px
    left calc(50% - 175px)
    border-radius 5px
    color #fff
    font-size 22.5px
    z-index 10000000
  .m-diabox
    width 350px
    height 175px
    background #fff
    position fixed
    z-index 100000001;
    top calc(50% - 87.5px)
    left calc(50% - 175px)
    .s-title,.s-money
      font-size 20px
      color #333
    .s-title
      height 40px
      line-height 40px
      border-bottom 2px solid #f1f1f1
    .s-money
      margin-top 10px
    .s-button
      width 116px
      height 28px
      line-height 28px
      background-color #de1616
      border-radius 3px
      margin 50px auto 0
      font-size 20px
      color #fff
    .s-close
      display block
      width 12px
      height 12px
      background url(../assets/images/close.png) no-repeat center center
      position absolute
      top 10px
      right 10px
  .slide-fade-enter-active, .slide-fade-leave-active
    transition: all .5s
  .slide-fade-enter, .slide-fade-leave-to
    transform: translateY(100px);
    opacity: 0;
</style>
